import mockdata from '@/util/category.js'
import treedata from '@/util/treedata.js'
import transfer from '@/util/transfer.js'

export default{
	data(){
		return {
			// form数据 
			create:{
				topics : '',

				meeting_feature : 'all' ,
				meeting_feature_option :[] ,

				meeting_level :'all',
				meeting_level_option : [],

				content :'',
				host:'',
				master:'',
				participant:[],

				site:'',

				meeting_time:'',
			},

			//参与人员选择 dialog
			participantVisible:false ,
			treeData :[],
			treeDataProps: {
         		children: 'children',
          		label: 'label'
        	},
        	transferData:[],

			//日期选择器 datepicker
			pickerOptions: {
          		shortcuts: [{
		            text: '最近一周',
		            onClick(picker) {
			              const end = new Date();
			              const start = new Date();
			              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
			              picker.$emit('pick', [start, end]);
			            }
		            }, {
		            text: '最近一个月',
		            onClick(picker) {
		              const end = new Date();
		              const start = new Date();
		              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
		              picker.$emit('pick', [start, end]);
		            }
		          	}, {
		            text: '最近三个月',
		            onClick(picker) {
		              const end = new Date();
		              const start = new Date();
		              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
		              picker.$emit('pick', [start, end]);
		            }
	          }]
	        },
		}
	},
	methods:{

		getTreeData(){
			let _this = this ;
			// console.log(_this.treeData)
			if(_this.treeData ){
				axios.post('/getTreeData',{})
				.then(function(response){
					var data = response.data.treedata ;
					// console.log(data)
					_this.treeData = data ;
				}).catch(function(error){
					console.log(error);
				})
			}
			_this.participantVisible = true ;
		},

		getEmployees(){
			let _this = this ;
			axios.post('/getEmployees',{})
			.then(function(response){
				const data = response.data.employees ;
				// _this.create.participant = data ;
				 // _this.transferData = data ;
				const transferData = [] ;
				
				for(let i=0 ; i<data.length ; i++){
					// console.log(data[i])
					transferData.push({
						key: data[i].id ,
						label : data[i].name ,
					});
					// console.log(transferData)
				}

				_this.transferData = transferData ;
				// console.log(_this.create.participant);
			
			}).catch(function(error){
				console.log(error);
			});
			this.$message('sdasd');
		}
	},
	created:function(){
			let _this = this ;

			axios.post('/getTableCategory',{})
			.then(function(response){
				var data = response.data ;
				_this.create.meeting_feature_option = data.meeting_feature_option ;
				_this.create.meeting_level_option = data.meeting_level_option ; 
			}).catch(function(error){
				console.log(error);
			});

			
	},

}




